import React, { useState } from 'react';
import PropTypes from "prop-types"
import { Fade } from "react-awesome-reveal"
import scrollTo from 'gatsby-plugin-smoothscroll'
import { slide as Menu } from 'react-burger-menu'
import styled from '@emotion/styled'
import { Link } from "gatsby"

const NavbarSection = ({ siteTitle }) => {
  const [menuState, setMenuOpen] = useState({ menuOpen: false });

  const closeMenu = () => {
    setMenuOpen({ menuOpen: false })
  }
  return (
    <>
      <div className="site-mobile-menu site-navbar-target">
        <div className="site-mobile-menu-header">
          <div className="site-mobile-menu-close mt-3">
            <span className="icon-close2 js-menu-toggle"></span>
          </div>
        </div>
        <div className="site-mobile-menu-body"></div>
      </div>
      <header className="site-navbar py-3 site-navbar-target" role="banner">
        <div className="container-fluid">
          <div className="row align-items-center justify-content-center">
            <div className=""></div>
            <Fade cascade triggerOnce>
              <nav className="site-navigation position-relative text-right" role="navigation">
                <ul className="site-menu main-menu js-clone-nav mr-auto d-none d-lg-block" style={{ marginTop: '15px' }}>
                  <li>
                    <a onClick={() => scrollTo('#home-section')} className="nav-link" >Home</a>
                  </li>
                  <li>
                    <a onClick={() => scrollTo('#about-section')} className="nav-link">About</a>
                  </li>
                </ul>
              </nav>

              <div className="text-center mx-5 ">
                <h2 className="m-0 site-logo" ><a onClick={() => scrollTo('#find-me-section')} className="nav-link">{siteTitle}</a></h2>
              </div>

              <div className="text-left">
                <nav className="site-navigation position-relative" role="navigation">
                  <ul className="site-menu main-menu js-clone-nav mr-auto d-none d-lg-block" style={{ marginTop: '15px' }}>
                    <li>
                      <a onClick={() => scrollTo('#Experience-section')} className="nav-link">Experience</a>
                    </li>
                    <li>
                      <a onClick={() => scrollTo('#certificate-section')} className="nav-link">Certificate</a>
                    </li>
                  </ul>
                </nav>

                <div className="d-inline-block d-lg-none" style={{ position: 'relative', top: '3px' }}>
                  <a href="#" className="site-menu-toggle js-menu-toggle float-right">
                    <span className="icon-menu h3"></span>
                  </a>
                </div>
              </div>
            </Fade>
          </div>
        </div>
      </header>
    </>
  )
}

NavbarSection.propTypes = {
  siteTitle: PropTypes.string,
}

NavbarSection.defaultProps = {
  siteTitle: ``,
}

const TopNav = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #fafafa;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1101;
  top: 0;
  width: 100vw;
`
const Logo = styled.div`
  padding: 1em 1em 0 1em;
`

const NavItems = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
  margin-top: 1em;
  a {
    color: #888;
    text-align: center;
    padding: 1em;
    text-decoration: none;
    font-size: 1em;
    &:hover {
      color: #333;
    }
  }
  @media(max-width: 721px) {
    display: none;
  }
`

const SideMenu = styled.div`
    /* Individual item */
    .bm-item {
      display: inline-block;
      /* Our sidebar item styling */
      text-decoration: none;
      margin-bottom: 10px;
      color: #d1d1d1;
      transition: color 0.2s;
    }
    /* Change color on hover */
    .bm-item:hover {
      color: white;
    }
    /* The rest copied directly from react-burger-menu docs */
    /* Position and sizing of burger button */
    .bm-burger-button {
      position: fixed;
      width: 36px;
      height: 30px;
      right: 36px;
      top: 36px;
    }
    /* Color/shape of burger icon bars */
    .bm-burger-bars {
      background: #373a47;
    }
    /* Position and sizing of clickable cross button */
    .bm-cross-button {
      height: 24px;
      width: 24px;
    }
    /* Color/shape of close button cross */
    .bm-cross {
      background: #bdc3c7;
    }
    /* General sidebar styles */
    .bm-menu {
      background: #373a47;
      padding: 2.5em 1.5em 0;
      font-size: 1.15em;
    }
    /* Morph shape necessary with bubble or elastic */
    .bm-morph-shape {
      fill: #373a47;
    }
    /* Wrapper for item list */
    .bm-item-list {
      color: #b8b7ad;
    }
    /* Styling of overlay */
    .bm-overlay {
      background: rgba(0, 0, 0, 0.3);
    }
    @media(min-width: 720px) {
      display: none;
    }
`

export default NavbarSection
