import * as React from "react"
import PropTypes from "prop-types"
import ImageGallery from 'react-image-gallery';
import 'react-image-gallery/styles/css/image-gallery.css';
import { Fade } from "react-awesome-reveal"

const CertificationSection = ({ data, title }) => (
    < React.Fragment >
        <section className="site-section bg-dark" id="certificate-section">
            <div className="container">
                <div className="row">
                    <div className="col-12 mb-5 position-relative">
                        <Fade direction="up" triggerOnce>
                            <h2 className="section-title text-center mb-5 text-white">
                                {title}
                            </h2>
                        </Fade>
                    </div>
                </div>
                <Fade direction="left" triggerOnce>
                    <div className="owl-carousel slide-one-item" style={{ marginTop: '-80px', marginBottom: '-80px' }}>
                        {getData(data)}
                    </div>
                </Fade>
            </div>
        </section>
    </React.Fragment >
)

function getData(data) {
    const ItemsArray = [];
    data.edges.forEach(item =>
        ItemsArray.push(
            {
                'original': item.node.image, 'thumbnail': item.node.image,
                'description': item.node.title + " [" + item.node.year + "]"
            }
        ));
    // return ItemsArray;
    return (
        <div className="slide">
            <blockquote>
                <ImageGallery items={ItemsArray} showBullets={false} />
            </blockquote>
        </div>
    )

}

CertificationSection.propTypes = {
    data: PropTypes.object.isRequired,
}

export default CertificationSection