import * as React from "react"
import PropTypes from "prop-types"
import { Fade, Slide } from "react-awesome-reveal"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faHeart,
    faEnvelope,
    faMapMarker,
    faChevronUp
} from '@fortawesome/free-solid-svg-icons'
import { faFacebookSquare, faInstagramSquare, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import scrollTo from 'gatsby-plugin-smoothscroll'

const FindmeSection = ({ title }) => (
    <footer className="site-section bg-light footer" id="find-me-section">
        <div className="container">
            <div className="row mb-5">

                <div className="container">
                    <div className="row" style={{ marginTop: '-5  0px' }}>
                        <div className="col-12 mb-5 position-relative">
                            <Fade direction="up" triggerOnce>
                                <h2 className="section-title text-center mb-5 text-black">
                                    {title}
                                </h2>
                            </Fade>
                        </div>
                    </div>
                    <div className="col-12 mb-5 position-relative" style={{ marginTop: '-80px', marginBottom: '-80px' }}>
                        <Slide direction="left" cascade triggerOnce>
                            <h5 className="d-inline-block d-md-block text-center"><a href="mailto:farchan@email.com"><FontAwesomeIcon icon={faEnvelope} /> Farchan@email.com</a></h5>
                            <h5 className="d-inline-block d-md-block text-center"><FontAwesomeIcon icon={faMapMarker} /> Cilacap, Indonesia</h5>
                        </Slide>
                        <Slide direction="up" duration={1200} delay={1200} cascade triggerOnce>
                            <p className="d-inline-block d-md-block text-center">
                                <a aria-label="facebook" href="https://facebook.com/aanuser" target="_blank" rel="noopener noreferrer" className="social-circle">
                                    <FontAwesomeIcon icon={faFacebookSquare} size="2x" />    </a>
                                <a aria-label="instagram" href="https://instagram.com/aanuser" target="_blank" rel="noopener noreferrer" className="social-circle">
                                    <FontAwesomeIcon icon={faInstagramSquare} size="2x" />    </a>
                                <a aria-label="linkedin" href="https://www.linkedin.com/in/mfarchana/" target="_blank" rel="noopener noreferrer" className="social-circle">
                                    <FontAwesomeIcon icon={faLinkedin} size="2x" />    </a>
                            </p>
                        </Slide>
                    </div>
                </div>
            </div>
        </div>

        <div className="row">
            <div className="col-12 text-center">
                <Slide direction="down" duration={1200} delay={1400} cascade triggerOnce>
                    <p>
                        Copyright &copy; {(new Date().getFullYear())} All rights reserved | This template is made with <FontAwesomeIcon icon={faHeart} aria-label="heart" className="icon-heart text-danger" aria-hidden="true" /> by
                        <a href="https://colorlib.com" target="_blank" rel="noopener noreferrer"> Colorlib</a>
                    </p>
                </Slide>
            </div>
        </div>
        <a onClick={() => scrollTo('#home-section')} className="back-to-top">
            {/* <i><FontAwesomeIcon icon={faChevronUp} /></i> */}
            <i className="icon-chevron-up"></i>
        </a>
        <div id="preloader">
            <div className="loader" id="loader-1"></div>
        </div>
    </footer >
)

FindmeSection.propTypes = {
    name: PropTypes.string,
}

FindmeSection.defaultProps = {
    siteTitle: ``,
}

export default FindmeSection
