import * as React from "react"
import PropTypes from "prop-types"
import { Fade, Slide } from "react-awesome-reveal"
import { StaticImage } from "gatsby-plugin-image"

const AboutSection = ({ name, title }) => {
    return (
        <div className="site-section" id="about-section">
            <div className="container">
                <div className="row">
                    <div className="col-12 mb-4 position-relative">
                        <Slide direction="down" triggerOnce>
                            <h2 className="section-title">{title}</h2>
                        </Slide>
                    </div>
                    <div className="col-lg-4 order-1 order-lg-2 mb-4 mb-lg-0">
                        <div className="bg-light pt-5">
                            <Fade triggerOnce>
                                <StaticImage src="../../assets/images/aan.png" alt="M. Farchan A."
                                    className="img-fluid"
                                    placeholder="none" />
                            </Fade>
                        </div>
                    </div>
                    <div className="col-lg-4 order-2 order-lg-1">
                        <Fade cascade triggerOnce>
                            <p>
                                Hello. My name is {name}. My nickname is
                                Aan. I was born in Semarang, August 14th 1999. Completed my Bachelor's degree in Information Engineering from Dian Nuswantoro University.
                            </p>
                            <p>
                                Graduated with a GPA of 3.87 and experienced as an assistant lecturer in the basic laboratory for 1 year and I have a lot of experience in various competitions and freelance project.
                            </p>
                        </Fade>
                    </div>
                    <div className="col-lg-4 order-3 order-lg-3">
                        <Fade cascade triggerOnce>
                            <p>
                                I am very enthusiastic about the development of mobile phones, I
                                also like UI / UX. I always follow the trend to make the
                                interface simple and easily understood by users.
                            </p>
                            <p>
                                At the front end I developed using flutter (OpenSource Framework
                                by Google). And for the back end I used to use Firebase (Google
                                Services)
                            </p>
                            <p>
                                <a href="#Experience-section" className="btn smoothscroll btn-primary">My Experience</a>
                            </p>
                        </Fade>
                    </div>
                </div>
            </div>
        </div>
    )
}

AboutSection.propTypes = {
    name: PropTypes.string,
}

AboutSection.defaultProps = {
    name: ``,
}

export default AboutSection
