/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import "./layout.css"
import '@assets/css/bootstrap.min.css';
import '@assets/css/style.css';
import '@assets/css/icomoon/style.css';
import NavbarSection from "./section/navbarSection";
import WelcomeSection from "./section/welcomeSection";
import AboutSection from "./section/aboutSection";
import ExperienceSection from "./section/experienceSection";
import CertificateSection from "./section/certificateSection";
import FindmeSection from "./section/findmeSection";

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query getData {
      site {
        siteMetadata {
          title,
          name,
        }
      }
      allExperienceJson {
        edges {
          node {
            job
            title
            description
            year
            image
          }
        }
      }
      allCertificationJson{
        edges{
          node{
            title
            year
            image
          }
        }
      }
    }
  `)

  // console.log(data.allCertificationJson.edges)
  return (
    <>
      <body data-spy="scroll" data-target=".site-navbar-target" data-offset="300">
        <div className="site-wrap">
          <NavbarSection siteTitle={data.site.siteMetadata?.title || `Title`} />
          <WelcomeSection name={data.site.siteMetadata?.name || `Name`} />
          <AboutSection name={data.site.siteMetadata?.name} title={'About Me'} />
          <ExperienceSection data={data.allExperienceJson} title={'My Experience'} />
          <CertificateSection data={data.allCertificationJson} title={'Certificate'} />
          <FindmeSection title={'Find Me'} />
        </div>
      </body>
      {/* <Dots /> */}
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
