import * as React from "react"
import PropTypes from "prop-types"
import { Fade } from "react-awesome-reveal"
import { Chrono } from "react-chrono";

const ExperienceSection = ({ data, title }) => (
    < React.Fragment >
        <section className="site-section" id="Experience-section">
            <div className="container">
                <div className="row mb-5">
                    <div className="col-12 position-relative">
                        <Fade direction="up" triggerOnce>
                            <h2 className="section-title text-center">{title}</h2>
                        </Fade>
                    </div>
                </div>
                <div className="row no-gutter">
                    {getData(data)}
                </div>
            </div>
        </section>
    </React.Fragment >
)

function getData(data) {
    // const ItemsArray = [];
    // data.edges.forEach((item, index) => {
    //     if (index % 2 == 0) {
    //         ItemsArray.push(
    //             <div className="item brand col-12 col-sm-6 col-md-6 col-lg-4 col-xl-6 mb-4" key={item.node.title}>
    //                 <Slide direction="left" >
    //                     <a href={item.node.image} className="item-wrap fancybox">
    //                         <span className="icon-search2"></span>
    //                         <img alt={item.node.title} className="img-fluid wow fadeInDown" data-wow-delay="0.4s" src={item.node.image} loading="lazy" />
    //                     </a>
    //                     <a className="wow fadeInDown" data-wow-delay="0.4s">{item.node.description}
    //                     </a>
    //                 </Slide>
    //             </div>
    //         )
    //     } else {
    //         ItemsArray.push(
    //             <div className="item brand col-12 col-sm-6 col-md-6 col-lg-4 col-xl-6 mb-4" key={item.node.title}>
    //                 <Slide direction="right" >
    //                     <a href={item.node.image} className="item-wrap fancybox">
    //                         <span className="icon-search2"></span>
    //                         <img alt={item.node.title} className="img-fluid wow fadeInDown" data-wow-delay="0.4s" src={item.node.image} loading="lazy" />
    //                     </a>
    //                     <a className="wow fadeInDown" data-wow-delay="0.4s">{item.node.description}
    //                     </a>
    //                 </Slide>
    //             </div>

    //         )
    //     }
    // });
    const ItemsArray = [];
    data.edges.forEach((item) => {
        ItemsArray.push({
            title: item.node.year,
            cardTitle: "  " + item.node.job,
            cardSubtitle: "  [" + item.node.title + "]",
            cardDetailedText: item.node.description,
            media: {
                type: "IMAGE",
                source: {
                    url: item.node.image
                }
            }
        })
    }
    );

    return (
        <Fade cascade triggerOnce>
            <Chrono items={ItemsArray}
                mode="VERTICAL_ALTERNATING"
                hideControls
                useReadMore={false}
                scrollable
                theme={{ primary: "#207561", secondary: "white", textColor: "#207561" }}
            />
        </Fade>);
}

ExperienceSection.propTypes = {
    data: PropTypes.object.isRequired,
}

export default ExperienceSection