import * as React from "react"
import PropTypes from "prop-types"
import { Slide } from "react-awesome-reveal"
import { StaticImage } from "gatsby-plugin-image"
import scrollTo from 'gatsby-plugin-smoothscroll'

const WelcomeSection = ({ name }) => (
    <div className="site-blocks-cover overlay bg-light" id="home-section">
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-md-12 mt-lg-5 text-left align-self-center text-intro">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="contents">
                                <Slide cascade triggerOnce>
                                    <h1 className="text-black">Hello !</h1>
                                    <p className="lead">{name}</p>
                                    <p>
                                        <button onClick={() => scrollTo('#about-section')} className="btn btn-primary">About-Me</button>
                                    </p>
                                </Slide>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div>
            <StaticImage src="../../assets/images/aan.png"
                alt="M. Farchan A."
                className="img-face"
                placeholder="none"
                layout="fixed" />
        </div>
    </div>
)

WelcomeSection.propTypes = {
    name: PropTypes.string,
}

WelcomeSection.defaultProps = {
    siteTitle: ``,
}

export default WelcomeSection
